.card {
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
    width: 200px;
    background-color: lawngreen;
  }
  
  .card:hover {
    box-shadow: 0 8px 16px 0 rgba(233, 35, 35, 0.795);
  }
  
  .container {
    padding: 2px 16px;
}

img{
    width: 200px;
    height: auto;
}